<!--
   JSX  render 
  JSX  React  <  HTML  {  JS 
-->
<script>
  import AnchoredHeading from './anchoredHeading'

  export default {
    components: {
      AnchoredHeading
    },
    render (h) { // <-- h must be in scope
      return (
        <AnchoredHeading level={this.level}>
          *<span>{this.$slots.default}</span>*
        </AnchoredHeading>
      )
    },
    props: {
      level: {
        type: Number,
        default: 3
      }
    }
  }
</script>



