<!--
  render  VNodes ,
   Vnodes 
   v-if, v-for  JS 
-->
<script>
  export default {
    render (createElement) {
      let _self = this
      return createElement(
        'div',
        Array.apply(null, {length: this.times}).map(function () {
          return createElement('span', _self.$slots.default)
        })
      )
    },
    props: {
      times: {
        type: Number,
        default: 20
      }
    }
  }
</script>



